<template>
  <div class="hello">
    <Toolbar />
    <div class="tasks">
      <el-table
        :data="det"
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
      >
        <el-table-column prop="name" label="名称" sortable width="400">
        </el-table-column>
        <el-table-column prop="length" label="大小" sortable></el-table-column>

        <el-table-column prop="percent" label="进度" sortable>
        </el-table-column>
        <el-table-column prop="status" label="状态" sortable> </el-table-column>
        <el-table-column label="操作" width="145">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="$router.push(`/tasks/${scope.row.gid}`)"
              >详情</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Toolbar from "./Toolbar"
export default {
  components: { Toolbar },
  name: "Downloading",
  data() {
    return {
      status: "=",
      tasks: [],
      det: [],
      intId: null,
    }
  },
  props: {},
  computed: {
    aria2() {
      return this.$store.state.aria2
    },
  },
  methods: {
    async update() {
      // console.log("刷新stat tasks")
      try {
        this.status = await this.aria2.getGlobalStat()
        // 获取下载中和暂停的数据
        this.tasks = [...(await this.aria2.tellStopped(0, 100))]
        //连接成功后设置获取数据
        if (!this.intId) {
          this.intId = setInterval(this.update, 1000)
        }
      } catch (e) {
        this.tasks = []
        this.det = []
        clearInterval(this.intId)
        this.intId = null
      }
    },
  },
  async mounted() {
    //通过监听ws从服务器传回数据开头为onDownloadStart,给eventemitter发射,实时调用回调
    let aria2on = [
      "DownloadStart",
      "DownloadPause",
      "DownloadComplete",
      "DownloadStop",
      "BtDownloadComplete",
    ]
    aria2on.forEach((it) => {
      this.aria2.on(it, () => {
        this.update()
      })
    })
    this.update()
    this.intId = setInterval(this.update, 1000)
  },
  watch: {
    aria2() {
      // console.log("更新数据")
      clearInterval(this.intId)
      this.intId = null
      this.update()
    },
    // 将tasks读取出详情到det
    tasks() {
      // console.log("tasks改变")
      console.log(this.tasks)
      if (!this.tasks[0]) this.det = []
      this.tasks
        .filter((it) => it.status != "complete")
        .forEach((it, idx) => {
          let status = "停止"
          if (it.status == "paused") status = "暂停"
          if (it.status == "active") status = "下载中"
          if (it.status == "error") status = "停止"
          this.$set(this.det, idx, {
            name: it.files[0].path.split("/").at(-1) || "未知名称",
            percent:
              it.completedLength == 0
                ? 0
                : ((it.completedLength / it.totalLength) * 100).toFixed(2),
            status,
            gid: it.gid,
            length: countKb(it.totalLength),
          })
        })
      function countKb(n) {
        //parseFloat()可以去掉末尾的0
        if (n > 1073741824) return parseFloat((n / 1073741824).toFixed(2)) + " GB"
        if (n > 1048576) return parseFloat((n / 1048576).toFixed(2)) + " MB"
        if (n > 1024) return parseFloat((n / 1024).toFixed(2)) + " KB"
        return n + " B"
      }
    },
  },
  beforeDestroy() {
    clearInterval(this.intId)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 100%;
  height: 100%;
}
.status {
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  box-shadow: 0 0 1px grey;
}
.tasks {
  width: 100%;
  height: calc(100% - 50px);
  overflow: auto;
}
</style>
