<template>
  <div class="hello">
    <div class="toolbar"><div class="toolbar-i">设置</div></div>
    <div class="servers">
      <div class="title">
        <span>服务器列表</span
        ><el-button
          type="text"
          @click="
            dialogVisible = true
            addPop()
          "
          >添加</el-button
        >
      </div>

      <el-dialog
        title="添加Aria2服务器"
        :visible.sync="dialogVisible"
        width="50%"
      >
        <el-form label-position="left" label-width="80px" :model="newServer">
          <el-form-item label="名称">
            <el-input v-model="newServer.name"></el-input>
          </el-form-item>
          <el-form-item label="Host">
            <el-input v-model="newServer.host"></el-input>
          </el-form-item>
          <el-form-item label="Port">
            <el-input v-model="newServer.port"></el-input>
          </el-form-item>
          <el-form-item label="secret">
            <el-input v-model="newServer.secret"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="
              dialogVisible = false
              addServer()
            "
            >确 定</el-button
          >
        </span>
      </el-dialog>

      <el-table :data="servers" stripe style="width: 100%;">
        <el-table-column prop="name" label="名称"> </el-table-column>
        <el-table-column prop="host" label="host"> </el-table-column>
        <el-table-column prop="port" label="port"></el-table-column>
        <el-table-column prop="secret" label="secret"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="
                editVisible = true
                editPop(scope.$index)
              "
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="deleteServer(scope.$index)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="编辑服务器" :visible.sync="editVisible" width="50%">
        <el-form label-position="left" label-width="80px" :model="editServer">
          <el-form-item label="名称">
            <el-input v-model="editServer.name"></el-input>
          </el-form-item>
          <el-form-item label="Host">
            <el-input v-model="editServer.host"></el-input>
          </el-form-item>
          <el-form-item label="Port">
            <el-input v-model="editServer.port"></el-input>
          </el-form-item>
          <el-form-item label="secret">
            <el-input v-model="editServer.secret"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editVisible = false">取 消</el-button>
          <el-button
            type="primary"
            @click="
              editVisible = false
              updateServer()
            "
            >确 定</el-button
          >
        </span>
      </el-dialog>
    </div>
    <div class="more">
      文档:
      <a
        href="https://aria2.github.io/manual/en/html/aria2c.html?highlight=json#id3"
        >aria2 jsonrpc</a
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      dialogVisible: false,
      newServer: {
        name: "新服务器",
        host: "127.0.0.1",
        port: "6800",
        secret: "密令",
      },
      editVisible: false,
      editIdx: 0,
      editServer: {
        name: "",
        host: "",
        port: "",
        secret: "",
      },
      upServer: {},
    }
  },
  props: {
    msg: String,
  },
  computed: {
    servers() {
      return this.$store.state.servers
    },
  },
  methods: {
    addServer() {
      this.$store.commit("addServer", this.newServer)
    },
    // 点击添加时 重置内容
    addPop() {
      this.newServer = {
        name: "新服务器",
        host: "127.0.0.1",
        port: "6800",
        secret: "密令",
      }
    },
    deleteServer(idx) {
      this.$store.commit("deleteServer", idx)
    },
    // 点击编辑时显示对应数据
    editPop(idx) {
      this.editIdx = idx
      this.editServer = { ...this.servers[idx] }
    },
    updateServer() {
      this.$store.commit("updateServer", {
        idx: this.editIdx,
        server: this.editServer,
      })
    },
  },
  watch: {
    //
    servers(servers) {
      localStorage.setItem("aria2Servers", JSON.stringify(servers))
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width:100%;
  height:100%;
}
.toolbar {
  display: flex;
  justify-content: flex-start;
  align-content: space-around;
  align-self: baseline;
  height: 40px;
  font-size: 16px;
  padding: 5px;
  box-shadow: 0 0 2px grey;
}
.toolbar-i {
  vertical-align: middle;
  line-height: 30px;
  margin: 5px;
  height: 30px;
}
.title {
  padding: 0 20px 0 20px;
  height: 50px;
  line-height: 50px;
  display: flex;
  justify-content: space-between;
}
.servers {
  height:calc(100% - 250px);
  overflow: auto;
}
.more {
  padding: 10px;
}
.more a{
  color:rgb(24, 149, 158);
}
</style>
