<template>
  <div class="hello">
    <Toolbar />
    <div class="status" v-if="ifStatus">
      正在下载: {{ status.numActive }} 下载速度:
      {{ status.downloadSpeed }} 上传速度: {{ status.uploadSpeed }}
    </div>
    <div class="status" v-else>
      {{ status }}
    </div>
    <div class="tasks">
      <el-table
        :data="det"
        style="width: 100%"
        
      >
        <el-table-column prop="name" label="名称" sortable> </el-table-column>
        <el-table-column prop="length" label="大小" sortable width="80">
        </el-table-column>
        <el-table-column prop="percent" label="进度" sortable width="120">
          <template slot-scope="scope">
            <el-progress
              :percentage="scope.row.percent"
            ></el-progress>
          </template>
        </el-table-column>
        <el-table-column prop="downloadSpeed" label="速度" sortable width="80">
        </el-table-column>
        <el-table-column prop="status" label="状态" sortable width="80">
        </el-table-column>
        <el-table-column label="操作" width="210">
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.status == '下载中'"
              size="mini"
              type="success"
              @click="act('pause',scope.row.gid)"
              >暂停</el-button
            >
            <el-button
              v-else
              size="mini"
              type="primary"
              @click="act('unpause',scope.row.gid)"
              >开始</el-button
            >
            <el-button
              size="mini"
              @click="$router.push(`/tasks/${scope.row.gid}`)"
              >详情</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Toolbar from "./Toolbar"
export default {
  components: { Toolbar },
  name: "Downloading",
  data() {
    return {
      ifStatus: false,
      status: "获取数据中",
      tasks: [],
      det: [],
      intId: null,
    }
  },
  props: {},
  computed: {
    aria2() {
      return this.$store.state.aria2
    },
  },
  methods: {
    async update() {
      // console.log("刷新stat tasks")
      try {
        this.status = await this.aria2.getGlobalStat()
        this.ifStatus = true
        // 获取下载中和暂停的数据
        this.tasks = [
          ...(await this.aria2.tellActive()),
          ...(await this.aria2.tellWaiting(0, 100)),
        ]
        //连接成功后设置获取数据
        if (!this.intId) {
          this.intId = setInterval(this.update, 1000)
        }
      } catch (e) {
        this.ifStatus = false
        this.status = "获取数据失败"
        this.tasks = []
        this.det = []
        clearInterval(this.intId)
        this.intId = null
      }
    },
    async handleDelete(n) {
      try {
        this.det[n].status == "已请求删除"
        let gid = this.det[n].gid
        await this.aria2.remove(gid)
        this.update()
        console.log("删除成功")
      } catch (e) {
        console.log(e)
      }
    },
    async act(action,gid) {
      try {
        await this.aria2[action](gid)
        this.update()
      } catch (e) {
        console.log(e)
      }
    },
  },
  async mounted() {
    //通过监听ws从服务器传回数据开头为onDownloadStart,给eventemitter发射,实时调用回调
    let aria2on = [
      "DownloadStart",
      "DownloadPause",
      "DownloadComplete",
      "DownloadStop",
      "BtDownloadComplete",
    ]
    aria2on.forEach((it) => {
      this.aria2.on(it, () => {
        this.update()
      })
    })
    this.update()
    this.intId = setInterval(this.update, 1000)
  },
  watch: {
    aria2() {
      // console.log("更新数据")
      clearInterval(this.intId)
      this.intId = null
      this.update()
    },
    // 将tasks读取出详情到det
    tasks() {
      console.log("tasks改变", this.tasks)
      this.det = []
      if (!this.tasks[0]) this.det = []
      this.tasks.forEach((it, idx) => {
        let status = "未知状态"
        if (it.status == "paused") status = "暂停"
        if (it.status == "active") status = "下载中"
        let speed =
          it.downloadSpeed > 1024
            ? (it.downloadSpeed / 1024).toFixed(2) + " KB/s"
            : it.downloadSpeed + " B/s"
        let percent =
          it.completedLength == 0
            ? 0
            : ((it.completedLength / it.totalLength) * 100).toFixed(2)
        this.$set(this.det, idx, {
          name: it.files[0].path.split("/").at(-1),
          downloadSpeed: speed,
          percent,
          status,
          gid: it.gid,
          length: countKb(it.totalLength),
        })
      })
      function countKb(n) {
        //parseFloat()可以去掉末尾的0
        if (n > 1073741824)
          return parseFloat((n / 1073741824).toFixed(2)) + " GB"
        if (n > 1048576) return parseFloat((n / 1048576).toFixed(2)) + " MB"
        if (n > 1024) return parseFloat((n / 1024).toFixed(2)) + " KB"
        return n + " B"
      }
    },
  },
  beforeDestroy() {
    clearInterval(this.intId)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 100%;
  height: 100%;
}
.status {
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  box-shadow: 0 0 1px grey;
}
.tasks {
  width: 100%;
  height: calc(100% - 90px);
  overflow: auto;
}
</style>
