<template>
  <div class="hello">
    <Toolbar />
    <div class="tasks">
      <div class="status">
        <div>
          进度: {{ this.taskDet.percent }}% 状态:
          {{ this.taskDet.status }} 大小: {{ this.taskDet.totalLength }} 分片数:
          {{ this.taskDet.numPieces }} 分片大小: {{ this.taskDet.pieceLength }}
        </div>
        文件夹: {{ this.taskDet.dir }}
      </div>
      <el-table
        :data="fileDet"
        style="width: 100%"
        :default-sort="{ prop: 'date', order: 'descending' }"
      >
        <el-table-column prop="name" label="包含文件" sortable>
        </el-table-column>
        <el-table-column prop="length" label="大小" sortable> </el-table-column>
        <el-table-column prop="completedLength" label="已完成" sortable>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Toolbar from "./Toolbar"
export default {
  components: { Toolbar },
  name: "Downloading",
  data() {
    return {
      status: "=",
      task: [],
      taskDet: {},
      fileDet: [],
      intId: null,
    }
  },
  props: {},
  computed: {
    aria2() {
      return this.$store.state.aria2
    },
  },
  async mounted() {
    try {
      console.log(this.$route.params.gid)
      this.task = await this.aria2.tellStatus(this.$route.params.gid)
    } catch (e) {
      console.log(e)
    }
  },
  watch: {
    aria2() {
      // console.log("更新数据")
    },
    // 将tasks读取出详情到det
    task() {
      // console.log("tasks改变")
      console.log(this.task)
      // 数据
      // flies 文件数据 [completedLength length path selected]
      // tracker服务器 announcelist ['url','url',...]
      // dir 下载文件夹 ""
      //gid
      // numPieces 分片数
      // pieceLength 分片大小
      // totalLength 分片*大小合计

      if (!this.task) this.taskDet = []
      let speed =
        this.task.downloadSpeed > 1024
          ? (this.task.downloadSpeed / 1024).toFixed(2) + " KB/s"
          : this.task.downloadSpeed + " B/s"
      let percent =
        this.task.completedLength == 0
          ? 0
          : ((this.task.completedLength / this.task.totalLength) * 100).toFixed(
              2
            )
      let status = "未知状态"
      if (this.task.status == "paused") status = "暂停"
      if (this.task.status == "active") status = "下载中"
      if (this.task.status == "complete") status = "已完成"
      if (this.task.status == "stop") status = "停止"
      this.taskDet = {
        dir: this.task.dir,
        speed,
        percent,
        status,
        totalLength: countKb(this.task.totalLength),
        numPieces: this.task.numPieces,
        pieceLength: countKb(this.task.pieceLength),
      }
      this.task.files.forEach((it, idx) => {
        this.$set(this.fileDet, idx, {
          name: it.path.split("/").at(-1),
          completedLength: countKb(it.completedLength),
          length: countKb(it.length),
          selected: it.selected,
        })
      })

      function countKb(n) {
        //parseFloat()可以去掉末尾的0
        if (n > 1073741824)
          return parseFloat((n / 1073741824).toFixed(2)) + " GB"
        if (n > 1048576) return parseFloat((n / 1048576).toFixed(2)) + " MB"
        if (n > 1024) return parseFloat((n / 1024).toFixed(2)) + " KB"
        return n + " B"
      }
    },
  },
  beforeDestroy() {},
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 100%;
  height: 100%;
}
.status {
  height: 100px;
  line-height: 50px;
  padding-left: 20px;
  box-shadow: 0 0 1px grey;
  overflow: auto;
}
.tasks {
  width: 100%;
  height: calc(100% - 140px);
  overflow: auto;
}
</style>
