<template>
  <div class="sidebar">
    <el-row class="tac">
      <el-col :span="12">
        <el-menu
          :default-active="'/'+this.$route.path.split('/')[1]"
          background-color="#32373f"
          text-color="white"
          active-text-color="#ffd04b"
          class="el-menu-vertical-demo"
          router
        >
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-user"></i>
              <span>{{ status }}</span>
            </template>
            <el-menu-item-group>
              <el-menu-item :class="'server'+i" :servers="servers" @click="changeServer(i)" :key="i" v-for="(it,i) in servers">[{{i+1}}] {{it.name}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="/downloading">
            <i class="el-icon-download"></i>
            <span slot="title">正在下载</span>
          </el-menu-item>
          <el-menu-item index="/completed">
            <i class="el-icon-circle-check"></i>
            <span slot="title">已完成</span>
          </el-menu-item>
          <el-menu-item index="/deleted">
            <i class="el-icon-delete"></i>
            <span slot="title">已删除</span>
          </el-menu-item>
          <el-menu-item index="/settings">
            <i class="el-icon-setting"></i>
            <span slot="title">设置</span>
          </el-menu-item>
            <el-menu-item index="/tasks" style="display:none">
            <i class="el-icon-setting"></i>
            <span slot="title">详情-隐藏</span>
          </el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
    <button @click="getStatus()">刷新</button>
  </div>
</template>

<script>
export default {
  name: "Sidebar",
  data() {
    return {
    }
  },
  props: {
    msg: String,
  },
  computed: {
    servers() {
      return this.$store.state.servers
    },
    // 获取连接状态
    status() {
      return this.$store.state.status
    },
    active() {
      return this.$store.state.active
    }
  },
  methods: {
    // 手动刷新连接状态
    getStatus() {
      this.$store.commit("getStatus")
    },
    changeServer(idx) {
      this.$store.commit("changeServer",idx)
      this.$store.commit("getStatus")
    }
  },
  mounted() {
    // 页面载入时刷新连接状态
    this.$store.commit("getStatus")
  },
  watch: {
    active(active) {
      this.getStatus()
      localStorage.setItem("aria2Servers-active", JSON.stringify(active))
      // let se = document.querySelector('.server'+this.active)
      // se.style.color = 'red'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.sidebar {
  display: flex;
  flex-flow: column;
  background-color: #424242;
  min-width: 160px;
  overflow: hidden;
}
.tac,
.el-menu {
  padding:0;
  width: 160px;
  background-color: #32373f;
}
.nowfocus:checked + div {
  background-color: white;
}
.main {
  flex-grow: 1;
  background-color: white;
}
.sidebar ~ div {
  width: 100%;
}
</style>
