<template>
  <div class="toolbar">
    <div class="toolbar-i">
      <el-dropdown :hide-on-click="false" @command="handleCommand">
        <span class="el-dropdown-link">
          新建下载<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="url">URL</el-dropdown-item>
          <el-dropdown-item command="bt">BT文件</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dialog
        title="添加URL"
        :modal="false"
        :visible.sync="urlVisible"
        width="80%"
        ><el-input
          type="textarea"
          :rows="10"
          placeholder="请输入内容"
          v-model="urlInput"
        >
        </el-input>
        <span slot="footer" class="dialog-footer">
          <el-button
            type="primary"
            @click="
              urlVisible = false
              addUrl()
            "
            >确 定</el-button
          >
        </span>
      </el-dialog>

      <el-dialog title="添加BT文件" :visible.sync="btVisible" width="80%">
        <form>
          <input type="file" accept=".torrent" @change="getbt" />
          <div class="btse" v-if="this.btParse">
            注释: {{ this.btParse.comment }}
            <select multiple v-model="btSelected">
              <option
                v-for="(it, idx) of btParse.files"
                :key="idx"
                :value="idx"
              >
                {{ it.name }}
              </option>
            </select>
          </div>

          <button @click="addbt">确定</button>
        </form>
      </el-dialog>
    </div>
    <div class="toolbar-i">
      <el-radio-group v-model="radio1" size="small">
        <el-radio-button label="unpauseAll">全部开始</el-radio-button>
        <el-radio-button label="pauseAll">全部暂停</el-radio-button>
      </el-radio-group>
    </div>
  </div>
</template>

<script>
import parseTorrent from "parse-torrent"
export default {
  name: "Toolbar",
  data() {
    return {
      radio1: "",
      urlVisible: false,
      urlInput: "",
      btVisible: false,
      btfile: "",
      btParse: "",
      btSelected: [],
    }
  },
  computed: {
    aria2() {
      return this.$store.state.aria2
    },
  },
  methods: {
    async trans(action) {
      console.log("执行操作中", action)
      try {
        await this.aria2[action]()
        await this.aria2.getGlobalStat()
      } catch (e) {
        console.log(e)
      }
    },

    handleCommand(command) {
      // this.$message("click on item " + command)
      if (command == "url") this.urlVisible = true
      if (command == "bt") this.btVisible = true
    },
    async addUrl() {
      this.urlInput.split("\n").map(async (it) => {
        await this.aria2.addUri([it.trim()])
      })
      this.urlInput = ""
    },
    getbt(e) {
      let bt = e.target.files[0]
      let fr = new FileReader()
      fr.addEventListener("load", () => {
        // bt文件内容
        // data:application/x-bittorrent;base64,ZDg6YWxxxxxxxxxxxxxxxx
        this.btfile = fr.result.slice(fr.result.indexOf(",") + 1)
        this.btParse = parseTorrent(Buffer.from(this.btfile, "base64"))
        console.log("p", this.btParse)
      })
      fr.readAsDataURL(bt)
    },
    async addbt() {
      await this.aria2.addTorrent(this.btfile, [], {
        selectFlie: this.btSelected,
      })
      this.btfile = ""
      this.btParse = ""
      this.btSelected = []
      // this.btVisible = false
    },
  },
  mounted() {},
  watch: {
    radio1(radio1) {
      this.trans(radio1)
    },
    btfile(btfile) {
      console.log(btfile)
    },
    btSelected(b) {
      console.log(b)
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.toolbar {
  display: flex;
  justify-content: flex-start;
  align-content: space-around;
  align-self: baseline;
  height: 40px;
  font-size: 16px;
  padding: 5px;
  box-shadow: 0 0 2px grey;
}
.toolbar-i {
  vertical-align: middle;
  line-height: 30px;
  margin: 5px;
  height: 30px;
}
.btse {
  display: flex;
  flex-flow: column;
}
</style>
