import Vue from "vue"
import Vuex from "vuex"
import Aria2Client from "./aria2-client"
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    aria2: new Aria2Client({
      host: "localhost",
      port: 6800,
      secret: "",
    }),
    servers: JSON.parse(localStorage.getItem("aria2Servers")) || [],
    status: "未连接",
    count: 0,
    active: JSON.parse(localStorage.getItem("aria2Servers-active")) || 0,
  },
  mutations: {
    addServer(state, newS) {
      console.log(newS, state.servers)
      state.servers = [...state.servers, newS]
    },
    deleteServer(state, idx) {
      state.servers = state.servers
        .slice(0, idx)
        .concat(state.servers.slice(idx + 1))
    },
    updateServer(state, { idx, server }) {
      console.log(idx, server)
      state.servers = state.servers
        .slice(0, idx)
        .concat(server)
        .concat(state.servers.slice(idx + 1))
    },
    async getStatus(state) {
      try {
        await state.aria2.ready() //ws已连接,不需要密钥
        await state.aria2.getGlobalStat() //需要密钥才能成功
        console.log("已连接")
        state.status = "已连接" + (state.active + 1)
      } catch (e) {
        console.log("未连接", e)
        state.status = "未连接" + (state.active + 1)
      }
    },
    changeServer(state, idx) {
      state.aria2.close()
      state.status = "切换中"
      state.active = idx
      state.aria2 = new Aria2Client(state.servers[idx])
    },
  },
})

export default store
