import EventEmitter from "events"

class Aria2Client extends EventEmitter {
  constructor(options) {
    super()
    this.options = options
    this.websocket = new WebSocket(
      `ws://${options.host}:${options.port}/jsonrpc`
    )

    // 连接成功resolve
    this.connectPromise = new Promise((resolve, reject) => {
      this.websocket.addEventListener("open", () => {
        console.log("连接成功,;")
        resolve()
      })
      this.websocket.addEventListener("error", (e) => {
        console.log("连接失败",e)
        reject(e)
      })
    })

    // this.websocket.addEventListener('connected', () => {
    //   this.connected = true
    // })
    // close = > false
    this.lastId = 1

    this.callbacks = {} // 存放每个请求的回调函数

    //  通信模式 :
    //   网页  -- JSONRPC -- Aria2

    this.websocket.addEventListener("open", (e) => {
      this.websocket.send(JSON.stringify(e.data))
    })

    this.websocket.addEventListener("message", (e) => {
      var data = JSON.parse(e.data)

      // 10-11
      // data.method === 'aria2.onXXX'
      if (data.method && data.method.startsWith("aria2.on")) {
        // 在client触发相应事件, 对应监听器可以调用回调
        // 触发用on绑定的事件    传aria2.onXXXXX  的 XXXXX
        this.emit(data.method.slice(8), ...data.params)

        // 触发client.onDownloadStart绑定的事件
        //  事件名称需要on,    传aria2.onXXXXX  的 onXXXX
        var handler = this[data.method.slice(6)]
        if (typeof handler === "function") {
          handler(...data.params)
        }
      }
      // } else {
      // }

      var callback = this.callbacks[data.id] // 通过响应消息的id 在cb里查出对应请求的回调
      delete this.callbacks[data.id] // 删除这个回调
      if (typeof callback == "function") {
        callback(data)
      }
    })
  }

  ready() {
    return this.connectPromise
  }
  close() {
    this.websocket.close()
    return new Promise((resolve) => {
      this.websocket.addEventListener("close", () => {
        resolve()
      })
    })
  }
}

[
  "addUri",
  "addTorrent",
  "getPeers",
  "addMetalink",
  "remove",
  "pause",
  "forcePause",
  "pauseAll",
  "forcePauseAll",
  "unpause",
  "unpauseAll",
  "forceRemove",
  "changePosition",
  "tellStatus",
  "getUris",
  "getFiles",
  "getServers",
  "tellActive",
  "tellWaiting",
  "tellStopped",
  "getOption",
  "changeUri",
  "changeOption",
  "getGlobalOption",
  "changeGlobalOption",
  "purgeDownloadResult",
  "removeDownloadResult",
  "getVersion",
  "getSessionInfo",
  "shutdown",
  "forceShutdown",
  "getGlobalStat",
  "saveSession",
].forEach((methodName) => {
  Aria2Client.prototype[methodName] = function (...args) {
    // return new Promise((resolve, reject) => {
    //   var xhr = new XMLHttpRequest()
    //   xhr.open('post', 'http://127.0.0.1:6800/jsonrpc')
    //   xhr.setRequestHeader('content-type', 'application/json')
    //   xhr.send(JSON.stringify(
    //       {
    //           jsonrpc: '2.0',
    //           id: this.lastId++,
    //           method: `aria2.${methodName}`,
    //           params: ['token:abcdefg', 0, 100],
    //       }
    //   ))
    //   xhr.onload = function () {
    //     var data = JSON.parse(xhr.responseText)
    //     if (data.error) {
    //     } else {
    //     }
    //   }
    // })

    //websocket 连接成功时
    return this.connectPromise.then(() => {
      return new Promise((resolve, reject) => {
        var id = this.lastId++
        this.callbacks[id] = function (data) {
          if (data.error) {
            reject(data.error)
          } else {
            resolve(data.result)
          }
        }
        if (this.websocket.readyState === this.websocket.OPEN) {
          this.websocket.send(
            JSON.stringify({
              jsonrpc: "2.0",
              id: id,
              method: `aria2.${methodName}`,
              params: [`token:${this.options.secret}`, ...args],
            })
          )
        } else {
          reject("WS_CONNECTION_ERROR")
        }
      })
    })
  }
})

export default Aria2Client
