<template>
  <div id="app">
    <div class="viewbox">
      <Sidebar />
      <div class="main">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
import Sidebar from "./components/Sidebar"
export default {
  name: "App",
  components: { Sidebar },
  data() {
    return {}
  },
  computed: {
    aria2() {
      return this.$store.state.aria2
    },
    servers() {
      return this.$store.state.servers
    },
    active() {
      return this.$store.state.active
    },
  },
  methods: {},
  mounted() {
    if (this.servers && this.servers[this.active]) {
      this.$store.commit("changeServer", this.active)
    }
    this.$store.commit("getStatus")
  },
}
</script>

